import React, { Component } from 'react';

const comentarios = [
    {
        texto: "Muito bom, gostei demais.",
        data: "10/04/2019",
        nome: "Lucas"
    },
    {
        texto: "Ótimo produto, funcionou muito bem e chegou rapidamente.",
        data: "10/04/2019",
        nome: "Ana"
    },
    {
        texto: "Bem fácil de montar e chegou muito rápido.",
        data: "10/04/2019",
        nome: "Joana"
    },
]

export default class Comentarios extends Component {

    renderComentarios(){
        return (
            <div className="comentarios flex horizontal">
                {
                    comentarios.map((item, idx) => (
                        <div className="comentario flex-1 flex vertical">
                            <div className="mensagem">
                                <span>{item.texto}</span>
                            </div>
                            <div className="dados flex flex-end">
                                <span className='dados-data'>{item.data} -&nbsp;</span>
                                <span className="dados-nome">{item.nome}</span>
                            </div>
                        </div>
                    ))
                }
            </div>
        )
    }

    render(){
        return (
            <div className="Comentarios">
                <div className="container">
                    <div className="flex vertical">
                        <div>
                            <h2>Comentários</h2>
                        </div>
                        { this.renderComentarios() }
                    </div>
                </div>
            </div>
        )
    }
}