import React, { Component } from 'react';

const produtos = [
    {
        img: `${process.env.PUBLIC_URL}/image/pic-1.png`,
        titulo: "Cloreto De Magnésio Pa 120 Cápsulas 500Mg - Meissen",
        preco: "R$ 27,46",
        precoSub: "ou 3x de R$ 9,63 sem juros"
    }
]

export default class ProdutosVisitados extends Component {

    renderProdutosVisitados(){
        return (
            <div className="produtos flex horizontal">
                {
                    produtos.map((item, idx) => (
                        <div key={idx} className="produto flex-1 flex vertical">
                            <div className="produto-imagem flex flex-center">
                                <img src={item.img} />
                            </div>
                            <div className="produto-titulo">
                                <h3>{item.titulo}</h3>
                            </div>
                            <div className="produto-precos flex vertical flex-center">
                                <h3>{item.preco}</h3>
                                <span>{item.precoSub}</span>
                            </div>
                        </div>
                    ))
                }
            </div>
        )
    }

    render(){
        return (
            <div className="ProdutosVisitados">
                <div className="container-big">
                    <div className="flex vertical">
                        <div className="flex flex-center">
                            <h2 className="text-center titulo-sub">
                                Produtos Visitados
                            </h2>
                        </div>
                        { this.renderProdutosVisitados() }
                    </div>
                </div>
            </div>
        )
    }
}